@import "../../colors.scss";
@import "../../common.scss";
@import "../../../node_modules/react-datetime/css/react-datetime.css";

.FinItemDetails {
  background-color: transparent; // test
  .Fin-Nav {
    position: relative;
    height: 30px;
    .Fin-Nav-Out {
      line-height: 30px;
      font-size: 14px;
      font-weight: bold;
      color: #fff;
      text-indent: 25px;
    }
    .Fin-Nav-Arrow {
      position: absolute;
      bottom: 0;
      left: 39px;
      transform: translateX(-50%);

      border-top: 0px;
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-bottom: 5px solid #fff;
    }
  }
  .Fin-WhiteBack {
    background-color: rgba($color: #fff, $alpha: 0.98);
    padding: 0 10px;
  }

  .Fin-Header {
    height: 60px;
    line-height: 60px;
    .Fin-SubCat {
      text-indent: 40px;
      float: left;
      padding: 5px 0;
      background-image: url("./category.png");
      background-size: 30px;
      background-position-y: center;
      background-repeat: no-repeat;
      .Category {
        font-size: 12px;
        line-height: 25px;
      }
      .SubCategory {
        font-size: 16px;
        font-weight: bold;
        line-height: 25px;
      }
    }
    .Fin-Amount {
      height: 100%;
      float: right;
      display: flex;
      align-items: center;
      input {
        inline-size: 200px;
        text-align: right;
        font-size: 20px;
        color: red;
        font-weight: bold;
        outline: none;
        border: 0;
        background: transparent;
      }
      .CalculatorIcon {
        width: 30px;
        height: 30px;
        background-image: url("./calculator.png");
        background-size: 20px;
        background-position: center;
        background-repeat: no-repeat;
        .CalculatorPanel {
          padding-top: 40%;
          position: fixed;
          width: 100%;
          left: 0;
          top: 0;
          height: 100%;
          z-index: 1;
          background-color: rgba(255, 255, 255, 0.96);
        }
      }
    }
  }
  .Fin-ComCats {
    .Fin-ComCat-Container {
      height: 56px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      // border-top: 1px dashed $GREY-LIGHT-II;
      // border-bottom: 1px dashed $GREY-LIGHT-II;
      .Fin-ComCat {
        display: inline-block;
        width: 100px;
        line-height: 60px;
        margin: 0 5px;
        @media screen and (max-width: 321px) {
          width: 80px;
        }
      }
    }
  }
  .Fin-Comment {
    height: 50px;
    line-height: 50px;
  }
  .Fin-Details {
    margin-top: 5px;
    // Rewrite search dropdown css
    .Dropdown-Input {
      border: 0px;
      border-bottom: 1px solid $GREY-LIGHT-II;
      border-radius: 0;
      background-color: #fff;
      .Dropdown-SearchImage {
        background-image: url("./details.png");
      }
    }
  }
  .Fin-Tags {
    padding: 0 10px 15px 0;
    .Fin-Tags-Add {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 50px;
      line-height: 50px;
      input {
        // @extend .MyWacai-Input;
        display: inline-block;
        height: 30px;
        line-height: 30px;
        width: 100%;
        outline: none;
        border: none;
        border-bottom: 1px solid $GREY-LIGHT-II;
        font-size: 16px;
        background: transparent;
        text-indent: 10px;
        width: 80%;
      }
      .Fin-Tags-Add-Btn {
        background-image: url("./add-tag.png");
        background-repeat: no-repeat;
        background-size: 30px;
        background-position: center;
        width: 30px;
        height: 30px;
        display: inline-block;
      }
    }
    .Fin-TagsManagement {
      max-height: 100px;
      overflow-y: scroll;
    }
  }
  .Fin-FullDetails {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 10px;
    .Fin-Place {
      flex-grow: 1;

      // Rewrite search dropdown css
      .Dropdown-Input {
        border: 0px;
        border-bottom: 1px solid $GREY-LIGHT-II;
        border-radius: 0;
        background-color: #fff;
        .Dropdown-SearchImage {
          background-image: url("./market-square.png");
        }
      }
    }
    .Fin-City {
      flex-grow: 1;
      margin-left: 15px;

      background-image: url("./geo-icon.svg");
      background-position: left center;
      background-size: 16px;
      background-repeat: no-repeat;
      border-bottom: 1px solid $GREY-LIGHT-II;
      input {
        height: 50px;
        line-height: 50px;
        inline-size: 100px;
        text-indent: 25px;
        font-size: 16px;
        outline: none;
        border: 0;
        background: transparent;
      }
    }
  }
  .Fin-Date {
    height: 30px;
    line-height: 30px;
    input {
      outline: none;
      border: 0;
      font-size: 16px;
    }
  }

  .Fin-Toolbar {
    padding: 20px 0;
    .Fin-Btns {
      margin: 0 auto;
      width: 80%;
      display: flex;
      justify-content: space-between;
      .Fin-Btn {
        border: 1px solid $GREY-LIGHT-II;
        border-radius: 5px;
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        font-weight: bold;
        min-width: 80px;
        text-align: center;
      }
    }
  }

  .PopupButtons--Container {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100vh;
  }
}
